            стан корупції в Україні, що проводилися з 2007 до 2021 року.
            Вибірка, розроблена для кожного з шести досліджень, є
            репрезентативною для дорослого населення (віком 18 років і старшим)
            України в цілому, а також для кожної області України. Дослідження
            проводились за випадковою багатоступінчастою вибіркою з квотним
            відбором на останньому етапі. На першому етапі формування вибірки в
            межах кожної області випадковим чином були відібрані населені пункти
            пропорційно до кількості населення в них. Другий етап передбачав
            випадковий відбір виборчих дільниць (у 2007 - 2011 роках – поштових
            відділень) на території відібраних населених пунктів. На території
            кожної з відібраних дільниць випадковим чином відбирались вулиці,
            будинки та квартири. Останнім етапом був відбір респондента в межах
            домогосподарства і безпосереднє проведення опитування. Отримані дані
            зіставлялись із даними про статево-вікову структуру населення за
            даними Державної служби статистики.
          </p>
          <p>
            Загальна вибірка кожного дослідження перевищує 10 тис. осіб.
            Теоретична похибка досліджень складає 1% для загальнонаціональної
            вибірки.
          </p>
          <p>
            Через те, що в полах питання про досвід корупції були сформульовані
            по-різному, за досвід корупції визначається згідно з питаннями про
            усі сфери.
          </p>
          <p>
            Оскільки сфери та органи влади змінювалися між першим і останнім
