          <p>
            Перелік сфер дещо змінювався, а саме: в 2018 році додалася сфера
            системи державних закупівель, якої не було в попередніх опитуваннях,
            зате зникла сфера ДАІ, функції якої були ширшими за функції
            сервісних центрів МВС. Для підрахування досвіду корупції були обрані
            тільки ті питання, що відображали функції ДАІ, які перебрали
            сервісні центр МВС (отримання водійських прав, проведення технічного
            огляду, реєстрація автомобіля). Через те, що питання «Чи стикалися
            ви зі ДАІ?» стосувалося ширшого переліку функції ліквідованого
            органу, рахувати показники від тих, хто стикався зі сферою, було б
            некоректним.
          </p>

          <p>
            Вважалося, що респондент мав досвід корупції у більшості зі сфер,
