            опитуванні немає можливості.
          </p>
          <p>
            У сферах охорони здоров’я, шкільної освіти та ДАІ (в опитуваннях до
            2015 року включно) був заданий інший перелік питань, із
            специфікацією можливого корупційного досвіду, та іншим переліком
            відповідей. Вважалося, що респондент має корупційний досвід, якщо
            його примушували дати хабар або придбати дещо, або ж якщо він
            добровільно давав хабар, аби отримати потрібний результат, навіть
