      TweenLite.to("#b3_2_1_area", 0.5, {
        attr: { d: newArea }
      });
    },

    //блок 3. linechart 3
    b3_line3_data: function dataChanged() {
      var newLine = this.d3LinePath(this.b3_line3_data.data);
      var newArea = this.d3AreaPath(this.b3_line3_data.data);
      TweenLite.to("#b3_3_1_line", 0.5, {
        attr: { d: newLine }
      });
      TweenLite.to("#b3_3_1_area", 0.5, {
        attr: { d: newArea }
      });
    }
  },
  mounted: function() {
    this.$nextTick(function() {
      window.addEventListener("load", this.sl1_position);
      window.addEventListener("load", this.sl2_position);
