      var map1_data = that.b1_general.filter(function(d) {
        return d.obl != "Ukraine" && d.year === that.actual_year && d.axis === "corruption";
      });

      map1_data.map(function(d) {
        if (d.value > 0) {
          d.percent = " " + Math.floor(d.value * 100) + "%";
        } else {
          d.percent = " немає даних";
        }
        d.ukrName = ukr_names[d.obl];
      });
      return map1_data;
    },
    b1_map1_rect: function() {
      var that = this;
      var map_rect = [];
      that.b1_map1_data.forEach(function(d) {
        if (!map_rect.includes(d.opacity)) {
          map_rect.push(d.opacity);
        }
      });
      map_rect.sort(function(a, b) {
        return (a === null) - (b === null) || +(a > b) || -(a < b);
      });
      return map_rect;
    },
    b1_line_data: function() {
      var that = this;
      var data = that.b1_general.filter(function(d) {
        if (d.value != 0) {
          return d.obl === "Ukraine" && d.axis === "corruption";
        }
      });

      var lastYear = data.filter(function(d) {
        return d.year === that.actual_year;
      });
      var max = d3.max(data, function(d) {
        return d.value;
      });
      var min = d3.min(data, function(d) {
        return d.value;
      });

      if (max - min < 0.1) {
        max = d3.max(lastYear, function(d) {
          return d.value;
        });
        min = d3.min(lastYear, function(d) {
          return d.value;
        });
      }

      data.map(function(d) {
        d.x = that.scale.x(d.year) - 10;
        d.y = that.scale.y(d.value) - 10;
      });

      data = data.sort(function(a, b) {
        return a.year - b.year;
      });

      return { data, max, min };
    },

