    //block 3 linechart data
    b3_line1_data: function() {
      var that = this;
      var data = that.b3_general.filter(function(d) {
        if (d.value != 0) {
          return (
            d.sphere === that.sphere &&
            d.obl === "Ukraine" &&
            d.axis === "dealt_with_sphere"
          );
        }
      });
      var lastYear = data.filter(function(d) {
        return d.year === that.actual_year;
      });
      var max = d3.max(data, function(d) {
        return d.value;
      });
      var min = d3.min(data, function(d) {
        return d.value;
      });

      if (max - min < 0.1) {
        max = d3.max(lastYear, function(d) {
          return d.value;
        });
        min = d3.min(lastYear, function(d) {
          return d.value;
        });
      }
      data.map(function(d) {
        d.x = that.scale.x(d.year) - 10;
        d.y = that.scale.y(d.value) - 10;
      });

      return { data, max, min };
    },
