  //custom directives
  directives: {
    axisY(el, binding, vnode) {
      d3.select(el).call(
        d3
          .axisLeft(vnode.context.scale.y)
          .tickValues(binding.value)
          .tickFormat(vnode.context.Format)
      );
    },
    axisX(el, binding, vnode) {
      d3.select(el).call(
        d3
          .axisBottom(vnode.context.scale.x)
          .tickValues([2007, 2009, 2011, 2015, 2018, 2021])
          .tickFormat(d3.format("d"))
      );
